import { getAuth, signInWithEmailAndPassword, signOut,createUserWithEmailAndPassword} from "firebase/auth";
import { doc,setDoc } from "firebase/firestore"; 
import 'firebase/auth'
import { initializeApp } from 'firebase/app'; 
import { getFirestore } from 'firebase/firestore'
import { firebaseConfig } from "../../config/fbConfig";

export const signUp = (newUser) =>{
  
    const auth = getAuth();
    const firebase = initializeApp(firebaseConfig)
    const firestore = getFirestore(firebase)
    return async(dispatch)=>{
        const user2=newUser.firstname
       createUserWithEmailAndPassword(auth, newUser.email, newUser.password)
          .then(async (resp) => {
           return setDoc(doc(firestore,'users',resp.user.uid),{
  
              firstName: user2,
              lastName: newUser.lastname,
              initials: newUser.firstname[0]+newUser.lastname[0]})
         
          }).then(() => {
            dispatch({ type: 'SIGNUP_SUCCESS'})
          
          }).catch(err =>{
            dispatch({ type: 'SIGNUP_ERROR',err})
          })
    }
}

const addcol = (firestore,resp) =>{
  setDoc(doc(firestore, "users", resp.user.uid), { 
    name: "Los Angeles", state: "CA", country: "USA" });
}

export const signIN = (credentials) =>{
    return (dispatch, getState,{getFirebase}) => {
      

    const auth = getAuth();
   
    signInWithEmailAndPassword(auth, credentials.email, credentials.password).then((cred) =>{
        console.log(cred.user)    
        dispatch({
                type: 'LOGIN_SUCCESS'});
            }).catch((err)=>{
                dispatch({
                    type: 'LOGIN_ERROR', err
                });
            });
        
      }
    }


    
export const signOUT = () => {
    const auth = getAuth();
    return (dispatch) => {
   
        signOut(auth).then(() => {
           
            dispatch({
                type: 'SIGNOUT_SUCESS'
            });
          }).catch((error) => {
            console.log(error);
          });
          
    }
}


