import { getAuth, signInWithEmailAndPassword, signOut,createUserWithEmailAndPassword} from "firebase/auth";
import { doc,getDoc } from "firebase/firestore"; 
import 'firebase/auth'
import { initializeApp } from 'firebase/app'; 
import { getFirestore } from 'firebase/firestore'
import { firebaseConfig } from "../../config/fbConfig";
export  const createProject = (project) => {
  
  
    return async(dispatch, getState, {getFirebase}) => {
      const auth = getAuth();
  const user = auth.currentUser;
  const firebase = initializeApp(firebaseConfig)
  const firestore = getFirestore(firebase)
  const docRef =  doc(firestore,"users", user.uid)
  const docSnap = await getDoc(docRef)
  const doc1 = docSnap.data()
  console.log("projects detail")
  console.log(doc1)
      const firestore1 = getFirebase().firestore();
      firestore1.collection('projects').add({
        ...project,
        authorFirstName: doc1.firstName,
        authorLastName: doc1.lastName,
        authorId: user.uid,
        createdAt: new Date()
      }).then(() => {
        dispatch({ type: 'CREATE_PROJECT', project: project })
      }).catch((error) => {
        dispatch({ type: 'CREATE_PROJECT_ERROR', error: error })
      })
    }
  };